body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
}

html {
  height: 100%;
}

main {
  background-color: #f1f1f1;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

#root {
  height: 100%;
}

.logo-bg {
  background-image: url('/img/logo-new.png');
  background-size: contain;
  background-repeat: no-repeat;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 64px;
  cursor: pointer;
}

.App-grid {
  height: 100%;
}

.container-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.container-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.flex-item {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
}

.flex {
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-positive: 1 !important;
      flex-grow: 1 !important;
}

.flex-row {
  -ms-flex-direction: row;
      flex-direction: row;
}

.flex-column {
  -ms-flex-direction: column;
      flex-direction: column;
}

.map-grid {
  padding: 10px;
}

.someComponent-grid {
  padding: 10px;
}

.dash-right-grid {
  padding: 10px;
}

.dash-left-grid {
  -ms-flex-wrap: nowrap !important;
      flex-wrap: nowrap !important;
}

.topGridContainer {
  height: 15%;
}

.bottomGridContainer {
  height: 85%;
}

.padded {
  padding: 10px;
}

.padded2x {
  padding: 20px;
}

.mapContainer {
  border-radius: 4px;
  background-color: #ffffff;
  margin-bottom: 10px !important;
}

.graphContainer {
  border-radius: 4px;
  background-color: #ffffff;
}

.graphDiv {
  padding-top: 10px;
}

.title {
  -ms-flex-positive: 0 !important;
      flex-grow: 0 !important;
  padding: 10px;
}

.mapDiv {
  width: 100%;
  height: 100%;
}

.loginpiccontainer {
  -ms-flex-item-align: stretch;
      align-self: stretch;
  padding-top: 8%;
  padding-bottom: 8%;
}

.loginpic {
  background-image: url('/img/WarGames-19832-1024x576.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
}

.LoadingContainer {
  width: 100%;
  height: 100%;
  background-color: #424242;
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.loadingSVG{
  width: 100px;
  height: 100px;
}

.placeholder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  color: white;
  background: #525963;
  font-size: 35px;
  height: 255px;
}

.bulbIcon {
  background-image: url(/img/bulb.png);
  width: 20px;
  height: 30px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.bulbIcon:hover {
  background-image: url(/img/bulb_hover.png);
}

.sensorIcon {
  background-image: url(/img/sensor.png);
  width: 20px;
  height: 30px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.sensorIcon:hover {
  background-image: url(/img/sensor_hover.png);
}

.switchIcon {
  background-image: url(/img/switch.png);
  width: 20px;
  height: 30px;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.switchIcon:hover{
  background-image: url(/img/switch_hover.png);
}

.h100 {
  height: 100%;
}

.w100 {
  width: 100%;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.communityStatus {
  width: 100%;
  height: 530px;
  overflow: auto;
}

.siteList {
  width: 100%;
  height: 530px;
  overflow: auto;
}


.deviceList {
  height: 255px;
  overflow: auto;
  margin-bottom: 29px !important;
}

.mapGrid {
  height: 255px;
  margin-bottom: 29px !important;
}

.notificationsGrid {
  height: 255px;
  margin-bottom: 20px !important;
}

@media screen and (min-height: 700px) {
  main {
    height: calc(100% - 64px) !important;
  }
}

@media screen and (min-height: 900px) {
  .communityStatus {
    height: auto;
    max-height: 711px;
  }
  .siteList {
    height: auto;
    max-height: 711px;
  }
  .mapGrid {
    height: 345.5px;
    margin-bottom: 29px !important;
  }
  .deviceList {
    height: 345.5px;
    margin-bottom: 29px !important;
    overflow: auto;
  }
  .notificationsGrid {
    height: 345.5px;
    margin-bottom: 20px !important;
  }
}



